import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global';
import { AuthService } from '../../auth/auth.service'
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-commontopprozpurpleheader',
  templateUrl: './commontopprozpurpleheader.component.html',
  styleUrls: ['./commontopprozpurpleheader.component.css']
})
export class CommontopprozpurpleheaderComponent implements OnInit {
  loginData: any;
  role: any;
  name: any;
  roleName: any;
  constructor(
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public toastr: ToastrService,
    private http: Http,
    public authService: AuthService,
    private spinner: NgxSpinnerService,
  ) { }

  ngOnInit() {
    this.loginData = JSON.parse(localStorage.getItem("login"))
    console.log("loginData"+this.loginData)
    this.name = this.loginData.name;
    console.log("loginData"+this.name)
    this.role = this.loginData.role;
    this.getRoleData(this.role)
  }

  getRoleData(roleCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getRoleData' + "/" + roleCode, options)
      .subscribe(data => {
        var response = data.json();
        if(response.status === 200){
          this.roleName = response.response[0].roleDescription
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }

}
