import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { VanlynkLayoutComponent } from '../vanlynk/vanlynk-layout/vanlynk-layout.component';
import { VanlynkDashboardComponent  } from '../vanlynk/vanlynk-dashboard/vanlynk-dashboard.component';



const routes: Routes = [
  {
    path: '', component: VanlynkLayoutComponent,
    children: [
      {
        path: 'vanlynkDashboard',
        component: VanlynkDashboardComponent
    }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class VanlynkRoutingModule { }
